import React, { useContext, useState } from 'react';
import "../styles/signup.css"
import { Link, useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { Shaka } from '../components/Shaka';
import { Modes } from '../shared/context/modes';
import { request } from "../apiHandler/Authapi";
import url from "../assets/lolo.png";
import { data } from "../shared/utils/data";
import { toast, ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { GoogleLogin } from "react-google-login";


function Signup() {
    const history = useHistory();

    const { loginMode, setUser, setLoginMode } = useContext(Modes);
    const [focus1, setFocus1] = useState(false);
    const [focus2, setFocus2] = useState(false);
    const [focus3, setFocus3] = useState(false);
    const [focus4, setFocus4] = useState(false);
    const [focus5, setFocus5] = useState(false);
    const [loading, setLoading] = useState(false);

    const [info, setInfo] = useState({
        firstname: "",
        lastname: "",
        email: "",
        password: "",
        retype: "",
        isValid: false
    });
    const [agreed, setAgreed] = useState(false);
    const [showError, setShowError ] = useState(false);
    const handleChange = e => {
        const { name, value } = e.target;
        setInfo({
            ...info,
            [name]: value
        })
    }
    const [checked, setChecked] = useState(false);
    const setLoginModeTrue = () => {
        setLoginMode(!loginMode);
        setLoading(false);
    };
    const { firstname, lastname, email, retype, password } = info;

    const handleSubmit = e => {
        if(!agreed){ setShowError(true)};
        e.preventDefault();
        setLoading(true);
        request('users/register', 'POST', { firstname, lastname, email, password, retype }, { "Content-Type": "application/json" })
            .then(data => {
                setLoading(false);
                if (data.message) {
                    setLoading(false);
                    // toast.success(data.message);
                    history.push("/verifyMessage");
                    // setSignupMode(false);
                }
                if (data.error) {
                    toast.error(data.error);
                }
            })
            .catch(err => {
              toast.error("Unexpected error occurred!");
            });

    }
    const handleLogin = e => {
        setLoading(true);
        e.preventDefault();
        request('users/login', "POST", { email, password }, { "Content-Type": "application/json" })
            .then(data => {
                setLoading(false);
                if (data.token) {
                    
                    setUser(data.user);

                    localStorage.setItem("auth", data.token);
                    localStorage.setItem("user", JSON.stringify(data.user));

                    history.push("/dashboard");
                }
                if (data.error) {
                    if(data.error === "Verify"){
                        toast.warning("Please verify your account");
                        history.push("/verifyMessage");
                    }else{
                        toast.error(data.error, {
                            style: {
                                fontSize: 12
                            }
                        })
                    }
                }
            })
            .catch(err =>{
                toast.error("Unexpected error occurred! Try again", {
                    style: {
                        fontSize: 12
                    }
                });
            })
    }
    const handleGoogleLogin = async googleData => {
        request("users/google-login", "POST", { token: googleData.tokenId }, {"Content-Type":"application/json"})
            .then((data) =>{ 

                if (data.token) {
                        localStorage.setItem("auth", data.token);
                        localStorage.setItem("user", JSON.stringify(data.user));
                        setUser(data.user);

                        history.push("/dashboard");
                    } else {
                        toast.error(data.error, {
                            style: {
                                fontSize: 12
                            }
                        });
                    }
            })
            .catch(err => {
                console.log("Err", err)
               toast.error("Unexpected error occurred!", {
                   style: {
                       fontSize: 12
                   }
               });
            });
    


    }
    return (
        <Holder>
            <Shaka url={url} />
            <ToastContainer position="top-center" autoClose={3000} />

            <Form className="form" autoComplete="off" onSubmit={loginMode ? handleLogin : handleSubmit}>
                <Sub className="sub">
                    <Title className={` title-login ${loginMode ? "m-top" : ""} `}>
                        {loginMode ? <h2>{data.login.header} </h2> : <h2>{data.signup.header}</h2>}
                        <p>{data.signup.statement}</p>
                    </Title>
                    <Control className="control">
                        {!loginMode && <Names className="names">
                            <Wrapper className="wrapper first">
                                <First
                                    className={firstname !== "" ? "valid" : ""} onFocus={() => setFocus1(true)}
                                    onBlur={() => setFocus1(false)}
                                    name="firstname"
                                    placeholder={focus1 ? "" : "FirstName"}
                                    value={firstname}
                                    onChange={handleChange}
                                    autoComplete="off"
                                    required
                                />
                                <label onFocus={() => setFocus1(true)} className={(focus1 || firstname !== "") ? "focus" : ""}>FirstName</label>
                            </Wrapper>
                            <Wrapper className="wrapper second">
                                <Second className={`name2 ${lastname !== "" ? "valid" : ""}`} onFocus={() => setFocus2(true)}
                                    onBlur={() => setFocus2(false)}
                                    name="lastname"
                                    placeholder={focus2 ? "" : "LastName"}
                                    value={lastname}
                                    onChange={handleChange}
                                    autoComplete="off"
                                    required
                                />
                                <label className={(focus2 || lastname !== "") ? "focus" : ""}>LastName</label>
                            </Wrapper>
                        </Names>}
                        <Input className="long">
                            <FormControl
                                type="email"
                                className={email !== "" ? "valid" : ""}
                                onFocus={() => setFocus3(true)}
                                onBlur={() => setFocus3(false)}
                                name="email"
                                placeholder={focus3 ? "" : "Email"}
                                value={email}
                                onChange={handleChange}
                                autoComplete="new-password"
                                required
                            />
                            <label className={(focus3 || email !== "") ? "focus" : ""}>Email</label>
                        </Input>
                        <Input className="long">
                            <FormControl
                                type="password"
                                className={password !== "" ? "valid" : ""}
                                onFocus={() => setFocus4(true)}
                                onBlur={() => setFocus4(false)}
                                name="password"
                                placeholder={focus4 ? "" : "Password"}
                                value={password}
                                onChange={handleChange}
                                autoComplete="new-password"
                                required
                            />
                            <label className={(focus4 || password !== "") ? "focus" : ""}>Password</label>
                        </Input>
                        {!loginMode && <Input className="long">
                            <FormControl
                                type="password"
                                className={retype !== "" ? "valid" : ""}
                                onFocus={() => setFocus5(true)}
                                onBlur={() => setFocus5(false)}
                                name="retype"
                                placeholder={focus5 ? "" : "Confirm Password"}
                                value={retype}
                                onChange={handleChange}
                                autoComplete="new-password"
                                required
                            />
                            <label className={(focus5 || retype !== "") ? "focus" : ""}>Confirm Password </label>
                        </Input>}
                        <Terms className="terms">
                           <Box type="checkbox" style={showError ? {border: "red"}: {}} onChange={()=>setAgreed(prev => !prev)} value={agreed} name="agree" id="agree" /> <label htmlFor="agree">{loginMode ? data.login.remember : data.signup.agree}</label>
                           <Link to="/forgotPassword" className={loginMode ? 'inline-block text-xs text-red-600 whitespace-nowrap ' : 'hidden'} >Forgot Password</Link>
                        </Terms>
                        <Register disabled = {!agreed && !loginMode } style={!agreed && !loginMode ? {background: "#5e94ffa1", cursor: "no-drop"} : {}}> {loading ? <Loader></Loader> : ""} <span className={loading ? "position" : ""}>{loginMode ? (loading ? "" : data.login.loginNow) : (loading ? "" : data.signup.create)}</span></Register>
                        <Links className="loginlinks">
                            <L1 className="l1"></L1>
                            <span>{loginMode ? data.login.loginWith : data.signup.signupWith}</span>
                            <L1 className="l1"></L1>
                        </Links>
                        <IconHandler className={loginMode ? "justify" : ""}>
                            <GoogleLogin
                                clientId="1022268755042-jeprqpcf9deq1cbtotans2bu4om4kugs.apps.googleusercontent.com"
                                buttonText={loginMode ? "Login with Google" : "Signup with Google"}
                                onSuccess={handleGoogleLogin}
                                onFailure={handleGoogleLogin}
                                cookiePolicy={'single_host_origin'}
                            />
                        </IconHandler>
                        <Login className={`normallogin ${!loginMode ? "login" : "signup"}`}>
                            <h3>{loginMode ? data.login.noAccount : data.signup.haveAccount}</h3> <Link to="/account" className="signin" onClick={setLoginModeTrue}>{loginMode ? data.login.signup : data.signup.signin}</Link>
                        </Login>
                    </Control>
                </Sub>
            </Form>
        </Holder>
    )

}
const Holder = styled.div`
    display: flex;
    background: #b0cbff;
    width: 100%;
    height: 100vh;
    @media screen and (max-width: 1335px){
        height: auto;
        .form{
            width: 55%;
        }
    }
    @media screen and (max-width: 1024px){
        width: 100%;
        display: flex;
        flex-direction: column;
        height: 100vh;
        .form{
            width: 80%;
            margin-top: 200px;
            height: 50vh;
            .normallogin{
                margin-top: 20px !important;
            }
            .sub{
                width: 80%;
                .terms{

                }
                .loginlinks span{

                    margin-left: 70px;
                }
                .l1{
                    width: 40%;
                }
            }
            .names{
                width: 100% !important;
                .first{
                margin-left: 0px !important;
                /* margin-right: 5px !important; */
            }
            .first{
               width: 48% !important;
               margin-right: 0px !important;
            }
           .second{
               margin-left: 35px !important;
                width: 45% !important;
            }
            }
        }
    }
    @media screen and (max-width: 768px){
        width: 100%;
        display: flex;
        flex-direction: column;
        height: 100vh;

        .form{
            width: 90%;
            height: 65vh;
            margin-top: 150px;
            .sub{
            width: 80% !important;
            .names{
                width: 100% !important;
                .first{
                margin-left: 0px !important;
                /* margin-right: 5px !important; */
            }
            .first{
               width: 48% !important;
               margin-right: 0px !important;
            }
           .second{
               margin-left: 20px !important;
                width: 42% !important;
            }
            }
        }
        }
    }
    @media screen and (max-width: 540px){
        .form{
            .sub{
                width: 90% !important;
                .terms{
                    label{
                        font-size: 15px !important;
                    }
                }
                .loginlinks span{
                     font-size: 14px !important;
                     margin-left: 10px;
                     width: 40%;
                }
                input{
                    padding: 3px 15px;
                }
                .first{
                    width: 80% !important;
                }
                .second{
                    width: 80% !important;
                    margin-right: 10px;
                }
            }
        }
    }
    @media screen and (max-width: 767px){
        display: flex;
        width: 100%;
        flex-direction: column;
        height: auto;
        div{
            width: 100%;
            font-size: 13px;
        }
        .form{
            width: 90%;
            margin-top: 200px;
            top: 100vh;
            padding: 10px;
            .first{
                margin-right: 15px;
            }
            font-size: 30px!important;
        }
    }
    @media screen and (max-width: 550px){
        height: auto;
        .form{
         height: 100vh;
        }
        span{
           font-size: 18px;
       }
       input::placeholder{
           font-size: 18px;
       }
       label{
           font-size: 18px;
       }
        .loginlinks > .l1{
         width: 35% !important;
        }
        .loginlinks {
            span{
            margin: 0 auto;
            margin-left: 10px;
            font-size: 18px;
        }
       span{
           font-size: 18px;
       }
        }
     .form{
         .normallogin{
             margin-top: 40px !important;
             font-size: 18px !important;
         }
     }
    }
    @media screen and (max-width: 420px){
        .form{
            height: 90vh !important;
        }
    }
    @media screen and (max-width: 450px){
        height: auto !important;
            .form{
                margin-top: 140px !important;
                /* height: 120vh !important; */
                .sub{
                    .control{
                        .names{
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        .second, .first{
                            width: 100% !important;
                            .name2{
                                margin: 0% !important;
                            }
                            margin-left: 0px !important;
                        }
                    }
                    .terms{
                        label{
                            font-size: 13px !important;
                        }
                    }
                    .normallogin{
                        width: 100%;
                        h3{
                            font-size: 15px;
                        }
                    }
                    .loginlinks{
                        width: 100%;
                        .L1{
                            width: 30% !important;
                        }
                        span{
                            padding: 0% !important;
                        }
                    }
                    }
                }
            }
    }
    @media screen and ( max-width: 414px){
        .form{
            margin-top: 200px !important;
            height: 100vh!important;
        }
    }
    @media screen and (max-width: 375px){
        .form{
            margin-top: 220px !important;
            height: 110vh !important;
        };
    }
    @media screen and (max-width: 360px){
        .form{
            margin-top: 120px !important;
            height: 125vh!important;
        }
    }
    @media screen and (max-width: 320px){
        width: 100%;
        .names{
            display: flex;
            flex-direction: column;
        }
        .form{
            margin-top: 150px !important;
         height: 130vh !important;
         .sub{
             .control{
                 .terms{
                     width: 100% !important;
                    label{
                        font-size: 10px !important;
                    }
                 }
                 .normallogin{
                        width: 100%;
                        h3{
                            font-size: 12px;
                        }
                    }
                .loginlinks{
                        width: 100%;
                        .l1{
                            width: 23% !important;
                        }
                        span{
                            margin-left: 10px !important;
                            text-align: center;
                            font-size: 13px !important;
                        }
                    }
             }
         }
        }
    }
    @media screen and (max-width: 280px){
        .form{
            margin-top: 180px !important;
           height: 120vh !important;
        }
    }
`
const Input = styled.div`
     width: 100%;
`
const Wrapper = styled.div `
     width: 48%;
     position: relative;
     input{
         width: 100%;
         height: 40px;
         padding: 5px;
     }
`
const Sub = styled.div`
   width: 90%;
   margin: auto;
   position: relative;
   div, input{
       margin-bottom: 10px;
   }
   input{
       padding: 5px 20px;
       height: 40px;
       background: #E8E6E6;
       outline: none;
       border: none;
       border-radius: 7px;
       position: relative;
   }
   
   input::placeholder{
        color: black;
        position: absolute;
        text-align: center;
    }
    input:focus {
        border: 3px solid dodgerblue;
        background: #ffffff;
        z-index: 1;
    }
    @media screen and (max-width: 768px){
     .title-login{
         font-size: 20px !important;
     }
     width: 100% !important;
    }
`

const Form = styled.form`
      width: 35%;
      margin: 20px auto;
      height: 95vh;
      background: #fff;
      border-radius: 10px;
      @media screen and (max-width: 768px){
          height: 85vh;
      }
`
const Title = styled.div`
      display: flex;
      flex-direction: column;
      font-family: 'poppins';
      justify-content: center;
      align-items: center;
      place-items: center;
      width: 100%;
      h2{
          text-align: center;
          color: #548dff;
          padding-top: 10px;
      }
      p{
          width: 60%;
          margin: 5px auto;
          font-size: 12px;
          text-align: center;
          opacity: 0.4;
      }
`
const Control = styled.div`
  display: flex;
  flex-direction: column;
  font-size: 14px !important;
`
const Names = styled.div`
     display: flex;
     justify-content:  space-around;
`
const First = styled.input`
    height: 40px;
    margin-right: 20px;
`
const FormControl = styled.input`
    width: 100%;
`
const Second = styled.input`
  width: 48%;
  @media screen and (max-width: 768px){
      width: 100% !important;
      margin-left: 10px;
  }
`
const Terms = styled.div`
    display: flex;
    width: 100%;
    align-items: center;
    font-family: 'poppins';
    input{
        width: 5%;
        margin-top: 10px;
        padding: 10px;
        height: 40px;
        margin-right: 10px;
    }
    @media screen and (max-width: 550px){
        align-items: center;
        label{
        font-size: 20px !important;

        }
    }
    input, label{ cursor: pointer;}
    label{
        width: 92%;
        font-size: 13px;
    }
`
const Register = styled.button`
  width: 100%;
  cursor: pointer6l;
  background: #5e93ff;
  text-align: center;
  align-items: center !important;
  place-items: center;
  color: #fff;
  /* padding: 10px; */
  height: 40px;
  border: none;
  border-radius: 3px;
  font-family: 'poppins';
  position: relative;
  .position{
      position: absolute;
      top: 10px;
      left: 130px;
  } 
`
const Box = styled.input`

`
const Links = styled.div`
     display: flex;
     align-items: center;
     span{
         width: 34%;
         padding: 0 20px;
         font-size: 12px;
     }
     margin-top: 20px; 
`
const L1 = styled.div`
    width: 35%;
    height: 2px;
    margin-top: 4px;
    background: black;
    opacity: 0.5;
    @media screen and (max-width: 280px){
        width: 20% !important;
    }

`
const IconHandler = styled.div`
     display: flex;
     align-items: center !important;
     justify-content: space-around;
     place-items: center!important;
     div{
         width: 10%;
         background: #fff;
         height: 30px;
         justify-content: center;
         place-items: center;
         box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
         border-radius: 10px;
         cursor: pointer;
         img{
             max-height: 100%;
             max-width: 100%;
         }
     }
`
const Login = styled.div`
 display: flex;
 align-items: center; 
 font-size: 16px;
 /* margin-top: 20px; */
 .signin{
     color: blue;
     text-decoration: none;
 }
 *{
     font-weight: normal;
     padding: 0px 2px;

 }
`
export const Loader = styled.div`
     height: 25px;
     width: 25px;
     border-radius: 50%;
     background: transparent;
     border: 3px solid lightgray;
     border-top: 3px solid #5e93ff;
     margin-left: 200px;
     margin-top: 5px;
     animation: spin 1s linear infinite;
     @keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@media screen and (max-width: 760px){
      margin-left: 200px !important;
      margin-top: 1px !important;
      height: 15px !important;
      width: 15px !important;
  }
  @media screen and (max-width: 450px){
      margin-left: 150px !important;
  }
  @media screen and (max-width: 390px){
      margin-left: 130px !important;
  }
@media screen and (max-width: 360px){
      margin-left: 120px !important;
  }
  @media screen and (max-width: 300px){
      margin-left: 90px !important;
  }
`
export default Signup
