import React,{ useState} from 'react'
import Sidebar from '../components/Sidebar';
import TopNav from '../components/TopNav';
import { useQuery } from "react-query";
import { API } from '../shared/context/modes';
import { categories } from '../shared/utils/Categories';
import Loader from '../components/Loader';
import { useParams } from 'react-router';


const ProdDetLost = () => {
    const productStyles = {
        backgroundColor: "#d5def5",
        marginLeft: "15%",
        minWidth:"85%",
        marginRight:"0",
        minHeight: "100vh"
    }
    const [product,setProduct] = useState({});
   
    
        const path = window.location.pathname
         const id = path.split('/')[2];
    const { data } =  useQuery("single", ()=> fetch(`${API}/lostProducts/${id}`, { method: "GET", headers: {"bearer": `${localStorage.getItem("auth")}`}}).then(res=>res.json()), {
        onSuccess: (data)=>{
            setProduct(data.product);

        },
        onError: (error)=>console.log(error)
    });

    const dateObj = new Date(product?.date);

    
    const [month,day,year] = dateObj.toLocaleDateString("US", { dateStyle: 'long' }).split(' ');
    return (
        <div className="flex">
            <Sidebar />
            <div className="p-2" style={productStyles}>
            <TopNav/>
        {product ? (
            <div className="bg-white rounded flex flex-col   sm:justify-start sm:flex-row mt-5 pr-2  sm:p-4 ">
           <div className="flex flex-col   p-1  sm:p-3 sm:mr-5   ">
               <div className="sm:w-96 w-full ">
             {  product._id ? <img src={categories.find(cat=> cat._id === product.category._id).image} alt="Product" className=" border rounded  mb-5 mx-1" /> : <Loader isLoading={true}></Loader>}
               </div>
               <div className="flex flex-nowrap justify-start ">
               {product._id && <img src={categories.find(cat=> cat._id === product.category._id).image} alt="Product"  className="rounded sm:w-24 w-1/3 h-24 mx-1" />}
               {product._id && <img src={categories.find(cat=> cat._id === product.category._id).image} alt="Product"  className="rounded sm:w-24 w-1/3 h-24 mx-1" /> }
              
              
               </div>
           </div>
            <div className="flex flex-col p-3 justify-start items-start">
                <div className="flex flex-col">
                    <h2 className="text-lg  mb-1 sm:text-2xl ">{product.category && product.category.name}</h2>
                    <span>Lost on {day.slice(0,1)}<sup>th</sup> {month}  {year}</span>
                </div>
                <p className="my-5 text-2xl text-gray-500">{product.category && product.category.price} Rwf</p>

                <p className="text-sm text-left ">
                    Lost at : {product.location}
                </p>

           <ul>
                    <li>
                        <span className="bold">Category: </span>
                        <span className="text-gray-500">Lost Product</span>
                    </li>
                    
                    <li>
                        <span className="bold">Tags: </span>
                        <span  className="text-gray-500">{product.category && product.category.name}</span>
                    </li>
                    
                </ul>
                                               <div>
                                    <span className="bold">More: </span>
                                    <span  className="text-gray-500">If you find this take it to the nearest police station</span>
                                </div>

            </div>
        </div>
        ): (<Loader isLoading={true}></Loader>)}
        </div>
        </div>
    )
}

export default ProdDetLost
