import styled from 'styled-components';
import {Link} from "react-router-dom";
export const Shaka = ({ url })=>{
    return(
        <Main>
          <Link to="/">
           <Logo>
           <svg xmlns="http://www.w3.org/2000/svg" width="40.314" height="40.314" viewBox="0 0 56.314 56.314">
         <path id="Path_2" data-name="Path 2" d="M46.441,42.521l11.873,11.87-3.923,3.923L42.521,46.441a24.955,24.955,0,1,1,3.92-3.92ZM30.215,16.352a11.089,11.089,0,1,0,7.335,7.335,5.544,5.544,0,1,1-7.335-7.338Z" transform="translate(-2 -2)" fill="#05f"/>
         </svg>

           <h2>Shaka</h2>
           </Logo>
           </Link>
           <Earn className="earn">
           <p></p>
               <h3>Earn money with <Link to="/"><span>shaka</span> </Link></h3>
           </Earn>
           <Find>
            <Msg></Msg>
            <Svg>
               <img src={url} alt=""/>
            </Svg>
           </Find>
           <Desc className="small">
               <p>
                  <Link to="/"> <span>Shaka</span> </Link> is online platform that helps the users to find their lost things by connecting them with the ones who found them.
               </p>
               <h3>Get paid for your help.</h3>
           </Desc>
        </Main>

    );
    
}
const Main = styled.div`
        width: 55%;
        margin-left: 20px;
        display: flex;
        flex-direction: column;
        position: relative;
        @media screen and (max-width: 1024px){
            width: 90%;
            .small{
                top: 15vh !important;
                width: 100%;
                overflow: hidden;
                z-index: 1;
                p{
                    font-size: 20px;
                    span{
                        font-size: 30px;
                    }
                }
            }

        }
        @media screen and (max-width: 540px){
            .small{
                top: 20vh !important;
            }
            .earn{
                h3{
                    font-size: 25px !important;
                    span{
                        font-size: 12px !important;
                    }
                }
            }
        }
        @media screen and (max-width: 768px){
            width: 100%;
            display: flex;
            flex-direction: column;
            font-size: 10px;
            padding-left: 50px;
            padding-top: 20px;
            margin: 0;
            .small{
                top: 30vh;
                margin-top: 50px;
                width: 80%;
                overflow: hidden;
                /* left: 1% !important; */
                z-index: 1;
                /* position: relative; */
                p{
                    font-size: 20px;
                    span{
                        font-size: 30px;
                    }
                }
            }
          .earn{
              h3{
                  font-size: 28px;
              }
              h3 span{
                  font-size: 36px !important;
              }
          }
        }
        @media screen and (max-width: 450px){
            .earn{
                display: flex;
                align-items: center;
                p{
                    margin-top: 3px !important;
                }
                h3{
                    font-size: 22px !important;
                    span{
                        font-size: 25px !important;
                    }
                }

            }
        }
        @media screen and (max-width: 360px){
            .earn{
                h3{
                    font-size: 14px !important;
                    span{
                        font-size: 15px !important;
                    }
                }
            }
        }
        @media screen and (max-width: 320px){
            width: 100% !important;
            margin-left: 0px !important;
            .earn{
                position: relative;
                h3{
                    /* position: absolute;
                    top: -32px;
                    left: 13%; */
                    font-size: 15px !important;
                    span{
                        font-size: 19px !important;
                    }
                }
            }
        }
        @media screen and (max-width: 280px){
            /* width: 100% !important; */
            .earn{
                position: relative;
                h3{
                    position: absolute;
                    top: -16px;
                    left: 13%;
                    font-size: 13px !important;
                    span{
                        font-size: 20px !important;
                    }
                }
            }
            .small{
                top: 15vh !important;
               p{
                font-size: 16px !important;
                span{
                    font-size: 17px !important;
                }
               }
            }
        }
    `;
const Logo = styled.div`
           width:  80%;
           margin: 30px auto;
           display: flex;
           align-items: center;
           color:  #3d7dff;
           font-family: 'poppins';
           width: 90%;

    `
const Find = styled.div`
     display: flex;
     flex-direction: column;
     width:  80%;
     margin: 30px auto;
`
const Svg = styled.div`
  img{
      height: 45vh;
      width: 90%;
  }
  @media screen and (max-width: 1024px){
            display: none;
        }
@media screen and(max-width: 320px){
    display: none;
    
}         
@media screen and (max-width: 768px){
    display: none;
}
`

const Msg = styled.div`
        
`
const Earn = styled.div`
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    font-family: 'poppins';
    @media  screen and (max-width: 768px){
        width: 90%;
        margin: auto;
       h3{
        font-size: 39px !important;
        span{
            font-size: 0px;
        }
       }

    }
    h3{
        font-weight: normal;
        span{
            color: #3333ff;
        }
    }
    p{
        width: 10%;
        height: 3px;
        background: #6666ff;
        opacity: 0.9;
        margin-right: 20px;
    }
`
const Desc = styled.div`
 width: 70%;
 margin: auto;
 font-family: 'poppins';
 position: absolute;
 left: 7%;
 top: 80vh;
 font-size: 16px;
 @media screen and (max-width: 768px){
     width: 90%;
     margin-left: 20px;
     font-size: 18px ;
     scroll-behavior: smooth;
 }
 p{
     span{
        color:  blue;
        font-size: 16px;
     }
 }
 h3{
     margin-top: 25px;
     font-weight: normal;
     font-size: 16px;
     opacity: 0.4;
 }
 @media screen and (max-width: 360px){
  p{
      font-size: 12px !important;
  }
  }
`