import React,{useState}  from "react";
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import {toast} from "react-toastify";
import Axios from "axios";
import treasure from "../assets/hunter.png";
import path from "../assets/path.png";
import contact from "../assets/contacts.png";
import Team from "./Team";
import Services from "./Services";

const HomePage= () =>{

    const [firstname,setFirstname]=useState("");
    const [lastname,setLastname]=useState("");
    const [email,setEmail]=useState("");
    const [message,setMessage]=useState("");
    const [isLoading,setIsLoading]=useState(false);


  

    const showMenu = ()=>{
       let menu = document.getElementById("mob-menu");
       console.log(menu)

       if(menu.classList.contains('hidden')){
           menu.classList.remove('hidden')
           menu.classList.add('flex')
       }else{
        menu.classList.remove('flex')
        menu.classList.add('hidden')
       }

       
    }

    const handleSubmit = async (e) =>{
      e.preventDefault();
      const feedbackObject={firstname:firstname,lastname:lastname,email:email,message:message};
      setIsLoading(true);
      try{

        let res=await Axios.post("https://shaka-1.herokuapp.com/api/users/feedback",feedbackObject);
        
        if(res.data.error){
          setIsLoading(false);
            toast.error(res.data.error);
        }

        if(res.data.message){
          setIsLoading(false);
            toast.success(res.data.message);
            setFirstname("");
            setLastname("");
            setEmail("");
            setMessage("");
        }

      }catch(err){
        setIsLoading(false);
        console.log(err);
        toast.error("Unexpected error occurred");
      }

    }

    return (
        <main > 
                       <ul className=" text-white text-2xl absolute z-50 justify-around items-center flex-col h-5/6 w-full opacity-95  bg-btn  hidden" id="mob-menu">
                       <button className="px-3 py-1 sm:hidden rounded bg-choco opacity-100" onClick={showMenu}>
                       <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
  <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
</svg>
         </button>
                <li className="hover:text-blue-700 mx-4 ">
                  <Link className="hover:text-blue-700 " to="#home">Home</Link>
                </li>
                <li className="hover:text-blue-700 mx-4 ">
                  <Link className="hover:text-blue-700 " to="/">About Us</Link>
                </li>
                <li className="hover:text-blue-700 mx-4 ">
                  <Link className="hover:text-blue-700 " to="/">Services</Link>
                </li>
                <li className="hover:text-blue-700 mx-4">
                  <Link className="hover:text-blue-700 " to="/">Contact Us</Link>
                </li>
                

           </ul>
    <nav className="flex justify-between px-5 py-3  items-center bg-primary opacity-95">
            <Link to='/'>
            <Logo>
           <svg xmlns="http://www.w3.org/2000/svg" width="40.314" height="40.314" viewBox="0 0 56.314 56.314">
         <path id="Path_2" data-name="Path 2" d="M46.441,42.521l11.873,11.87-3.923,3.923L42.521,46.441a24.955,24.955,0,1,1,3.92-3.92ZM30.215,16.352a11.089,11.089,0,1,0,7.335,7.335,5.544,5.544,0,1,1-7.335-7.338Z" transform="translate(-2 -2)" fill="#05f"/>
         </svg>
           <h1>Shaka</h1>
        </Logo>
        </Link>
        
           <ul className=" hidden sm:flex">
                <li className="hover:text-blue-700 mx-4 ">
                  <a className="hover:text-blue-700  " href="#home">Home</a>
                </li>
                <li className="hover:text-blue-700 mx-4 ">
                  <a className="hover:text-blue-700  " href="#about">About Us</a>
                </li>
                <li className="hover:text-blue-700 mx-4 ">
                  <a className="hover:text-blue-700  " href="#services">Services</a>
                </li>
                <li className="hover:text-blue-700 mx-4 ">
                  <a className="hover:text-blue-700  " href="#contact">Contact Us</a>
                </li>
                

           </ul>


           <div className="flex items-center ">
         <Link to="/account"> <button className="bg-btn mx-2 px-7 py-1 text-white rounded-lg" style={{padding:"7px",borderRadius:"3px"}}>Get Started</button></Link>
         <button className="px-3 py-1 sm:hidden rounded bg-choco opacity-100" onClick={showMenu}>
         <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h7" />
        </svg>
         </button>
             </div>
         
         
        </nav>


        <div className="flex justify-between overflow-hidden flex-col sm:flex-row sm:items-center py-3 sm:px-8 opacity-95 relative bg-primary sm:pl-14">
    
              <div className="flex-1 p-7 z-40 relative">
                  <h2 className="sm:text-5xl  text-3xl text-start  text-gray-700  mb-20">
                      Fastest &#38; Profitable way to find your  or others lost things.
                  </h2>
                  <p className="mb-12">
                  Shaka is online platform that helps the users to find their lost things by connecting them with the ones who found them.
                  </p>
                     <Link to="/account">
                     
                  <button className="px-8 py-2 rounded bg-choco opacity-100">Get Started</button>
                     </Link>
                  


              </div>
              <ImagePart>

              <div className="hidden relative z-30 sm:inline-block">
                  <img src={treasure} alt="Rich Icon" />
              </div>
              </ImagePart>


              <div className="absolute -bottom-28 -right-11  ">
              <img src={path} className="" alt="Rich Icon" />
              </div>
              
        </div>
        


         <Services id="services" />
         <Team/>

        
        <div className="my-5 opacity-100" id="contact">
        <h3 className="text-center text-3xl my-1 font-bold">Contact Our Team</h3>
            {/* <p className="mb-1 text-center">
                  Shaka is online platform that helps the users to find their <br /> lost things by connecting them with the ones who found them.
            </p> */}

            <div className="flex justify-around sm:px-8 flex-col sm:items-center sm:flex-row">
            <ImagePart>

              <div className="">
                  <img src={contact} alt="Rich Icon" />
              </div>
              </ImagePart>

            <div className="flex-1">
            <form className="" onSubmit={handleSubmit}>
                <h2 className=" text-center underline">Send us a message</h2>
                <div className="flex flex-col sm:flex-row mx-2 sm:mx-1">
                        <input type="text" className="rounded px-2 w-full mt-2 sm:w-1/2 outline-none sm:mx-1  py-2 border-0 bg-primary" 
                        placeholder="Firstname" onChange={(e) => setFirstname(e.target.value)} value={firstname} />
                        <input type="text" className="rounded px-2  w-full mt-2 sm:w-1/2 outline-white  py-2  border-0 bg-primary" 
                        placeholder="Lastname" onChange={(e) => setLastname(e.target.value)} value={lastname} />
                </div>
                <div className="flex-col flex mt-2"> 
                  <input type="email" className="rounded outline-none px-2 py-2 mx-2 border-0  bg-primary" 
                  placeholder="Email" onChange={(e) => setEmail(e.target.value)} value={email} />
                  <textarea placeholder="Your Message" name="feedback" id="feedback" value={message}
                  className="px-2 outline-none mx-2 bg-primary rounded mt-2 h-20"  cols="5" rows="10" onChange={(e) => setMessage(e.target.value)}></textarea>
                </div>
                <button className="float-right rounded py-1 mt-2 mx-2 px-6 opacity-100 " style={{background:"dodgerblue",color:"white"}}>
                 { !isLoading?"Send Message" : <Loader/>} </button>
              </form>
            </div>  
            </div>
        </div>
        
        <div className="border-t p-4" id="footer">
            <div className="flex justify-around flex-col sm:flex-row">
                <div className="flex flex-col">
                    <h2 className="font-bold my-3">Quick Links</h2>
                    <Link className="text-sm my-1" to="/">Home</Link>
                    <Link className="text-sm my-1" to="/">About Us</Link>
                    <Link className="text-sm my-1" to="/">Contact Us</Link>
                    <Link className="text-sm my-1" to="/">Services</Link>
                    <Link className="text-sm my-1" to="/">Account</Link>
                </div>
                <div className="flex flex-col">
                    <h2 className="font-bold my-3">Contact Info</h2>
                    <p className="text-sm my-1" to="/">Email: shaka1212@gmail.com</p>
                    <p className="text-sm my-1" to="/">Phone: +(250) 787340470</p>
                    <p className="text-sm my-1" to="/">Website: Shaka.rw</p>
                    <p className="text-sm my-1" to="/">Offices: Kicukiro, Kigali Rwanda</p>
                    
                </div>
                <div className="flex flex-col">
                    <h2 className="font-bold my-3">Developers</h2>
                    <Link className="text-sm my-1" to="/">Support</Link>
                    <Link className="text-sm my-1" to="/">Developer Panel</Link>
                    <Link className="text-sm my-1" to="/">Contributors</Link>
                    <Link className="text-sm my-1" to="/">Our apis</Link>
                    <Link className="text-sm my-1" to="/">Documentations</Link>
                </div>
                <div className="flex flex-col">
                    <h2 className="font-bold my-3">Why Choose us</h2>
                    <p className="text-sm my-1" to="/">Saving time on finding lost items</p>
                    <p className="text-sm my-1" to="/">Income Generations</p>
                    <p className="text-sm my-1" to="/">Finding your lost products</p>
                    <p className="text-sm my-4">
                    Shaka is online platform that helps the  <br /> users to find their  lost things by connecting <br />them with  the ones who found them.
                    </p>
                    
                </div>
                
            </div>
            <p className="text-center mb-1 mt-10">Copyright &copy; 2021 - Shaka. All Rights and Policies Reserved</p>
        </div>
        </main>
    )
}



const Logo = styled.div`
           width:  80%;
           margin: 10px auto;
           display: flex;
           align-items: center;
           color:  #3d7dff;
           font-family: 'poppins';
           width: 90%;

    `
const ImagePart = styled.div`
  flex:0.9 1 0;
  `
const Loader=styled.div`
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 5px solid lightgray;
  border-top: 5px solid #5e93ff;
  animation: spinner 1s linear infinite; 
  margin: 0px auto;

@keyframes spinner{
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}
`
export default HomePage;