import React, {useEffect} from 'react'
import {LinkedIn,Facebook, GitHub,WhatsApp} from '@material-ui/icons';
import prince from "../assets/prince.jpg"
import kamala from "../assets/kamala.JPG"
import manzi from "../assets/manzi.jpg"
import seth from "../assets/seth.JPG";
import Aos from "aos";
import "aos/dist/aos.css";

const Team = () => {
   
    useEffect(() =>{
        Aos.init({duration:1000});
    })

    const team = [
        {
            name : "Kamara Fidele",
            img: kamala,
            role: "CEO & Founder",
            facebook: "https://www.facebook.com/kamala.fidel/",
            linked:"https://linkedin.com",
            whatsapp:"https://web.whatsapp.com",
            github: "https://github.com/kamalafidele/"
        },
        {
            name : "Mutegetsi Prince",
            img: prince,
            role: "CTO & Co-Founder",
            facebook: "https://facebook.com",
            linked:"https://linkedin.com",
            whatsapp:"https://web.whatsapp.com",
            github: "https://github.com"
        },
        {
            name : "Mudahemuka Manzi",
            img: manzi,
            role: "CTO & Co-Founder",
            facebook: "https://facebook.com",
            linked:"https://linkedin.com",
            whatsapp:"https://web.whatsapp.com",
            github: "https://github.com"
        },
        {
            name : "Abijuru Seth",
            img: seth,
            role: "Product Designer",
            facebook: "https://facebook.com",
            linked:"https://linkedin.com",
            whatsapp:"https://web.whatsapp.com",
            github: "https://github.com"
        }
    ]
    return (
        
            <div className="my-16 " id="about">

            <h3 className="text-center text-3xl mt-4 font-bold" id="services">Our Team</h3>
            <p className="mb-7 text-center">
                Shaka was developed by a group of genius software engineers who are <br/> really interested in tech industry.
        
            </p>

            <div className="flex justify-around flex-col items-center sm:flex-row">

            {
                team.map((person,index)=>(
                    
                  <div className="flex-col relative transition-all group shadow-lg rounded-lg  w-72 mt-3 p-3  flex items-center justify-around" data-aos="fade-up" key={index}>         
                  <img src={person.img} className="h-48 w-40 border  rounded-full" alt="prince" />
                  <h2 className="text-xl bold my-4" >{person.name}</h2>
                  <span>{person.role}</span>
                  <div className="   bottom-0    w-full flex items-center" data-aos="fade-right">
                   <div className="opacity-100 flex w-full  items-center justify-center">

                      <a className="shadow-lg border-0 mx-2 rounded-full p-3 text-blue-600    bg-white" rel="noreferrer"  target="_blank" href={person.linked}>
                                <LinkedIn color="white"/>
                      </a>
                      <a className="shadow-lg border-0 mx-2 rounded-full p-3 text-black   bg-white" rel="noreferrer" target="_blank" href={person.github}>
                                <GitHub color="black"/>
                      </a>
                      <a className="shadow-lg border-0 mx-2 text-green-400 rounded-full p-3   bg-white" rel="noreferrer" target="_blank" href={person.whatsapp}>
                                <WhatsApp color="green"/>
                      </a>
                      <a className="shadow-lg border-0 mx-2 rounded-full p-3  text-blue-600  bg-white" rel="noreferrer" target="_blank" href={person.facebook}>
                                <Facebook color="white"/>
                      </a>

                     
                   </div>

                  </div>
                 
              </div>
                ))
            }
                  

                  
                  
                  
            </div>
            </div>
        

    )
}

export default Team
