import React,{ useState} from 'react'
import Sidebar from '../components/Sidebar';
import TopNav from '../components/TopNav';
import { useQuery } from "react-query";
import { API } from '../shared/context/modes';
import Loader from '../components/Loader';
import styled from 'styled-components';
import Modal from 'react-modal';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';

const ConfirmFoundProduct = () => {

    const [product,setProduct] = useState({});
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [choice,setChoice]=useState("");
    const history=useHistory();
   
    const path = window.location.pathname
    const id = path.split('/')[3]

    let clientWidth=window.screen.width;
    const customStyles = {
        overlay: {
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          zIndex:"3"
        },
          content: {
            top: clientWidth>400  ? '40%' : "40%",
            left: clientWidth>400 ? '50%' : "50%",
            right: clientWidth>400 ? '50%' : "10%",
            bottom: clientWidth>400 ? 'auto' : "auto",
            marginRight: clientWidth>400 ? '-50%' : "-55%",
            transform: 'translate(-50%, -50%)',
            background:"white"
          }
        };
        Modal.setAppElement('#root');
        let subtitle;
  
        function afterOpenModal() {
          // references are now sync'd and can be accessed.
          subtitle.style.color = '#f00';
        }
        
    
        function openModal() {
          setModalIsOpen(true);
        }
        function closeModal(){
            setModalIsOpen(false);
        }

    const productStyles = {
        backgroundColor: "#d5def5",
        marginLeft: "15%",
        minWidth:"85%",
        marginRight:"0",
        minHeight: "100vh"
    }
 
    
    const { data } =  useQuery("singleProduct", ()=> fetch(`${API}/foundProducts/${id}`, { method: "GET", headers: {"bearer": `${localStorage.getItem("auth")}`}}).then(res=>res.json()), {
        onSuccess: (data)=>{
            setProduct(data.product)
        },
        onError: (err)=>console.log(err)
    }) 

    if(choice=="1"){
         history.push("/payment")
    }else if(choice=="0"){
        history.push("/dashboard");
        toast.info("We shall continue to check for your item");
    }
   console.log(choice);
    

    const dateObj = new Date(product?.date)
    
    const [month,day,year] = dateObj.toLocaleDateString("US", { dateStyle: 'long' }).split(' ');
    return (
        <div className="big-div">
        <div className="flex">
            <Sidebar />
            <div className="p-2" style={productStyles}>
            <TopNav/>
        <div className="bg-white rounded flex flex-col   sm:justify-start sm:flex-row mt-5 pr-2  sm:p-4 ">
           <div className="flex flex-col   p-1  sm:p-3 sm:mr-5   ">
               <div className="sm:w-96 w-full ">
{ product.productImage ?   <img src={product.productImage} style={{cursor:"zoom-out"}} onClick={openModal} alt="Product" className=" border rounded  mb-5 mx-1" />: <Loader isLoading={true}></Loader>}
               </div>
               <div className="flex flex-nowrap justify-start ">
            {product.productImage && <img src={product.productImage} alt="Product"  className="rounded sm:w-24 w-1/3 h-24 mx-1" />}
            {product.productImage && <img src={product.productImage} alt="Product"  className="rounded sm:w-24 w-1/3 h-24 mx-1" />}
              
              
               </div>
           </div>
            <div className="flex flex-col p-3 justify-start items-start">
                <div className="flex flex-col">
                    <h2 className="text-lg  mb-1 sm:text-2xl ">{product.category && product.category.name}</h2>
                    <span>Found on {day.slice(0,1)}<sup>th</sup> {month}  {year}</span>
                </div>
                <p className="my-5 text-2xl text-gray-500">{product.category && product.category.price} Rwf</p>

                <p className="text-sm text-left ">
                    Found at : {product.location}
                </p>

                <ul>
                    
                    <li>
                        <span className="bold">Category: </span>
                        <span className="text-gray-500">Found Product</span>
                    </li>
                </ul>
                <Button onClick={openModal}>View Wide Image</Button>
                {/* <Button>Is it Yours?</Button> */}
                <label htmlFor="choid" style={{fontSize:"20px"}}>Is it yours?</label>
                <Select_Container>
                <select className="select-css" onChange={(e) => setChoice(e.target.value)}>
                  <option value="default">Choose</option>
                  <option value="1">Yes</option>
                  <option value="0">No</option>
                </select>
                </Select_Container>
            </div>
        </div>
        </div>
        </div>
        <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Upload picture"
      >
    <h2 className="hidden" ref={(_subtitle) => (subtitle = _subtitle)}>x</h2>
      <FontAwesomeIcon icon={faTimes} onClick={closeModal} style={{position:"absolute",top:"10px",right:"10px",color:"red",cursor:"pointer"}}/>
         <Image_Container>
        <img src={product.productImage} alt="view-large" />
        </Image_Container>
        </Modal>
        </div>
    )
}

const Button=styled.button`
  width: 80%;
  height: 45px;
  background: dodgerblue;
  color: white;
  text-align: center;
  border-radius: 4px;
  border: none;
  margin-top: 10px;
  margin-bottom: 10px;
`
const Select_Container=styled.div`
.select-css {
    display: block;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    width: 168px;
    line-height: 1.3;
    padding: .6em 1.4em .5em .8em;
    max-width: 100%; /* useful when width is set to anything other than 100% */
    box-sizing: border-box;
    margin: 0;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
    border-radius: 5px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
 
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
      linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
    background-repeat: no-repeat, repeat;
  
    background-position: right .7em top 50%, 0 0;

    background-size: .65em auto, 100%;
  }

  .select-css::-ms-expand {
    display: none;
  }

  .select-css:hover {
    border-color: #888;
  }

  .select-css:focus {
    border-color: #aaa;
   
    box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222; 
    outline: none;
  }
  

  .select-css option {
    font-weight:normal;
  }
  

  *[dir="rtl"] .select-css, :root:lang(ar) .select-css, :root:lang(iw) .select-css {
    background-position: left .7em top 50%, 0 0;
    padding: .6em .8em .5em 1.4em;
  }
  

  .select-css:disabled, .select-css[aria-disabled=true] {
    color: graytext;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22graytext%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
      linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
  }
  
  .select-css:disabled:hover, .select-css[aria-disabled=true] {
    border-color: #aaa;
  }
  
`
const Image_Container=styled.div`
 width: 100%;
 img{
     width: 100%;
 }
`

export default ConfirmFoundProduct
