import React, {useState} from "react";
import logo from "../assets/logo.png"
import styled from "styled-components";
import { Link, withRouter } from 'react-router-dom';
import "../styles/SidebarStyles.css";
import {FaBars} from "react-icons/fa";
import { faMoneyBill,  faSearch, faCheckDouble,faArchway,faExchangeAlt,faTimes,faHandHolding,faArrowAltCircleRight } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import Modal from "./Modal";
import UploadModal from "./Upload-modal";

const isActive = (history, path, showModal)=>{
    if(history.location.pathname === path && !showModal){

       return {color: "dodgerblue"}
    }else{
        return {color: "black"}
    }
}
const Sidebar= ({ history }) =>{

    
 let user=JSON.parse(localStorage.getItem("user"));

const [showModal, setShowModal] = useState(false);
const [showLostForm, setShowLost] = useState(false);
const [showFoundForm, setShowFound] = useState(false);
const [modalIsOpen, setModalIsOpen] = useState(false);
    
function openModal() {
  setModalIsOpen(true);
}


function closeModal(){
    setModalIsOpen(false);
}

const showFoundModal = () =>{
    setShowModal(!showModal);
    setShowFound(true);
    setShowLost(false);
}
const showLostModal = () =>{
    setShowModal(!showModal);
    setShowLost(true);
    setShowFound(false);
}

function logout(){
    localStorage.removeItem("auth");
    localStorage.removeItem("user");

    history.push("/account");
}

return(
   <>
        <SideBar style={modalIsOpen?{display:"none"}:{display:"block"}}>
             <input type="checkbox" id="burger"/>
            <div className="sidebar-container">
          <LogoWrapper>

            <label htmlFor="burger">
            <FaBars className="bars ico" />
            <FontAwesomeIcon icon={faTimes} className="cross ico"/>
            </label>

             <Wrapp>
             <img src={logo} style={{ height: 30, width: 30}} alt="Logo of shaka" className="logo-img"/>
            <h2 className="logo-name">Shaka</h2>
             </Wrapp>
            </LogoWrapper>
            <ul>
                <li>

                    <Link to="/dashboard" style={isActive(history, "/dashboard", showModal)}  className="a">
                        <FontAwesomeIcon icon ={faArchway} className="ico"/><span className="link-text">Dashboard</span></Link>
                </li>
                <li>
                    <Link onClick={ showLostModal } className="a"  
                    style={ showLostForm ? { color: "dodgerblue"}: {color: "black"}} to="#">
                        < FontAwesomeIcon icon={ faSearch} className="ico"/><span className="link-text">Find yours</span></Link>
                </li>
                <li>
                    <Link onClick={ showFoundModal } className="a"   
                    style={showFoundForm ? { color: "dodgerblue"}: {color: "black"}} to="#">
                    <FontAwesomeIcon icon ={faHandHolding} className="ico"/><span className="link-text">Submit found</span></Link>
                </li>
                <li>
                    <Link className="a"  to="/lostproducts" style={isActive(history, "/lostproducts", showModal)}>
                    <FontAwesomeIcon icon ={faExchangeAlt} className="ico"/><span className="link-text">Lost</span></Link>
                </li>
                <li> 
                    <Link className="a"  to="/foundproducts" style={isActive(history, "/foundproducts", showModal)}>
                    <FontAwesomeIcon icon ={faCheckDouble} className="ico"/><span className="link-text"> Found</span> </Link>
                </li>
                <li>
                    <Link className="a"  to="/payment" style={isActive(history, "/payment", showModal)}>
                    <FontAwesomeIcon icon ={ faMoneyBill } className="ico"/><span className="link-text"> Payment </span></Link>
                </li>

            </ul>

            <div className="mobile-profile">
                <img src={user.profile} alt="user-profile"/>
                <p className="mob-username">{user.firstname + " " + user.lastname}</p>

                <div className="mob-profile-details">
                <p className="mob-change-prof" onClick={openModal}><Link to="#">Change Profile </Link></p>
                <p className="logout" onClick={logout}>Logout <FontAwesomeIcon icon={faArrowAltCircleRight}/></p>
                </div>
                
            </div>
            <UploadModal closeModal={closeModal} modalIsOpen={modalIsOpen} />
            </div>

        </SideBar>
       <Modal showModal={ showModal } setShowModal={ setShowModal } showFoundForm={ showFoundForm } showLostForm={showLostForm} setShowFound={setShowFound} setShowLost={setShowLost} />   
   </>
    )
}


const SideBar=styled.div`

ul{
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 20% 12%;
}
ul li{
    margin-top: 15px;
    cursor: pointer;

}
ul li .a{
    display: flex;
    flex-direction: row;
    text-decoration: none;
    align-items: center;
    color: black;
    font-family: "poppins";
    span.link-text{
    padding-left: 10px;
    }
   
}
#burger{
    display: none;
}
.mobile-profile{
    display: none;
}
.mobile-profile img{
    width:40%;
    border-radius: 50%;
    margin: 10px auto;
    height: 80px;
}
.mobile-profile p{
    text-align: center;
}
.mob-profile-details{
    margin: 10px 0px;
}
.mob-profile-details p{
    padding: 5px 0px;
}


@media only screen and (max-width:620px){
    input[type=checkbox]:checked ~ .sidebar-container{
      .bars{
          display: none;
      }
      h2.logo-name{
        display: inline;
        transform: translateX(-10px);
    }
    .logo-img{
        display: inline;
        transform: translateX(-10px);
    }
    span.link-text{
        display: inline;
    }
    div.mobile-profile{
       display: block;
     }
    }
 

    ul li{
    margin-top: 25px;
}

}


@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ratio: 1) {
    
    input[type=checkbox]:checked ~ .sidebar-container{
        .bars{
            display: none;
        }
        span.link-text{
        display: inline;
        }
        h2.logo-name{
        display: inline;
        transform: translateX(-10px);
        }
        .logo-img{
        display: inline;
        transform: translateX(-10px);
        }
      }

      ul li{
    margin-top: 25px;
}

} 
`
const Wrapp = styled.div`
display: flex;
align-items: center;

`

const LogoWrapper=styled.div`
margin-top: 10%;
display: flex;
h2{
    padding-top: 0px;
    color: blue;
}
.bars{
    display: none;
}

justify-content: flex-start;
margin-left: 10%;
align-items: center;
@media only screen and (max-width:620px){
    .bars{
        display: block;
    }
}
@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ratio: 1) {
    .bars{
        margin: 0px 2px;
        display: block;
    }
}
`

export default withRouter(Sidebar);