import React,{useState,useEffect} from "react";
import { Notifications } from "@material-ui/icons"
import { faCaretDown, faCaretUp, faArrowAltCircleRight } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import styled from "styled-components"
import Axios from "axios";
import UploadModal from "./Upload-modal";
import { useHistory } from "react-router";
import { faTimes } from "@fortawesome/free-solid-svg-icons";


const ProfileTopNav = ({margin}) =>{
    
    const history=useHistory();
    const user=JSON.parse(localStorage.getItem("user"));
    const token=localStorage.getItem("auth");
    ///console.log(token);

    let [showProfile,setShowProfile]=useState(false);
    let [notifications,setNotifications]=useState([]);
    let [showNotification,setShowNotification]=useState(false);

//   BEGINNING OF MODAL STUFFS
 
      const [modalIsOpen, setModalIsOpen] = useState(false);
    
      function openModal() {
        setModalIsOpen(true);
        setShowProfile(false);
      }
    


    function changeStatus(){
        setShowNotification(false);
        setShowProfile(!showProfile);
    }

    function logout(){
        localStorage.removeItem("auth");
        localStorage.removeItem("user");

        history.push("/account");
    }

    useEffect(() =>{
           Axios.get("https://shaka-1.herokuapp.com/api/notifications",{headers:{bearer:token}})
           .then(res =>{
               setNotifications(res.data.notifications);
           })
    },[token]);
   
    function changeDisplay(){
           setShowProfile(false);
           setShowNotification(!showNotification);
    }

   function closeModal(){
       setModalIsOpen(false);
   }

   async function deleteNotification(id){
        

          Axios.delete(`https://shaka-1.herokuapp.com/api/notifications/${id}`,{headers:{bearer:token}})
          .then(_=>{
            let newNotifications=notifications.filter(notification => notification._id !== id);
            //notifications=notifications.filter(notification => notification._id !=id);
            setNotifications(newNotifications);
          })
   }

    return (
       <TopNav className="flex justify-end">
           {/* <div className="bg-white w-96 mx-2 rounded p-2 my-3">
               Welcome Prince
           </div> */}
        <div className="profile-container" style={{marginTop:margin}}>
        <div className="notification-container" onClick={changeDisplay} >
           <Notifications className="icon"></Notifications>
           <div className="notification-counter" style={notifications.length > 0 ? {background:"red", display: "block"}:{display: "none"}}>
                   <span>{notifications.length}</span>
           </div>
           <div className="notification-messages" style={showNotification ? {display:"flex",flexDirection:"column"} : {display:"none"}}>
                 <h1 style={{color:"white"}}>Notifications</h1>
               { notifications.length!==0 ? notifications.map(notification =>{
                   return (
                       <div style={{display:"flex"}} key={notification._id}>
                       <span key={notification._id}  className="notification">
                           {notification.notificationMessage}
                       </span>
                       <FontAwesomeIcon className="close" icon={faTimes} style={{color:"dodgerblue", display:"inline",marginLeft:"10px",fontSize:"20px"}} onClick={() =>deleteNotification(notification._id)}/>   
                      </div>
                   )
               }) : <span className="notification">You don't have notifications</span>}
           </div>
        </div>
        <div className="user-detail-container">
           <img src={user.profile} alt="The user profile" id="profile" onClick={changeStatus}/>
           <FontAwesomeIcon icon={showProfile ? faCaretUp : faCaretDown} 
           onClick={changeStatus}
           className="fa-caret"/>
           <ProfileDetails style={showProfile ? {display:"block"} : {display:"none"}}>
              <img src={user.profile} id="profile-detail-image" alt="user-profile"/>
              <p>{user.firstname + " " + user.lastname}</p>
              <div id="settings">
               <p className="p-for-profile-change" onClick={openModal}><Link to="#">Change profile Picture</Link></p>
               <p className="logout" onClick={logout}>Logout <FontAwesomeIcon icon={faArrowAltCircleRight}/> </p>
              </div>
           </ProfileDetails>
        </div>
        <UploadModal closeModal={closeModal} modalIsOpen={modalIsOpen}/>
    </div>
    </TopNav>
    )
}

const TopNav=styled.div`
 
  margin-left: 14rem;
  /* justify-content: flex-end; */

 div.profile-container{
     width: 7rem;
     display: flex;
     flex-direction: row;
     background-color: white;
     margin: 0px 17px;
     justify-content: space-evenly;
     border-radius: 5px;
     align-items: center;
     padding: 2px;
 }
 .fa-caret{
  position: absolute;
  left: 100%;
  top: 10px;
  font-size: 20px;
  cursor: pointer;
 }
 div.profile-container img#profile{
     width: 35px;
     height: 35px;
     border-radius: 50%;
     cursor: pointer;
 }
 div.profile-container #bell{
     width: 20px;
     cursor: pointer;
 }

 .notification-container{
     position: relative;
     height: 30px;
     width: 30px;
     align-items: center;
     justify-content: center;
     border-radius: 50%;
     background: dodgerblue;
     cursor: pointer;
     text-align: center;
     .icon{

         color: #fff;
     }
 }
 .notification-container .notification-counter{
     width: 14px;
     text-align: center;
     color: white;
     font-size: 12px;
     padding: 0%;
     margin: 0%;
     align-items: center;
     justify-content: center;
     height: 14px;
     border-radius: 50%;
     position: absolute;
     right: 0px;
     top: 0px;
 }
 .notification-messages{
     position: absolute;
    right: 40%; 
     top: 37px;
     background: #3d3b38;
     
     font-weight: bold;
     width: 250px;
     transition: 3s ease;
     border-radius: 3px;
     padding: 10px;
     font-size: 14px;
 }
 .notification{
     padding: 5px;
     color: white;
     font-size: 12px;
     :hover{
        color: dodgerblue;
       }
 }
 .close:hover{
    color: red !important;
} 

 .user-detail-container{
     position: relative;
 }
 @media only screen and (max-width:620px){
  display: block;
  margin-left: 2%;

 div.profile-container{
   display: none;
 }
 }
 @media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ratio: 1){
    margin-left: 3%;

 div.profile-container{
     margin: 4px 10px;
     width:20%;
 }
 .fa-caret{
  top:20%;
  right: 4.2%;
 }
 }
`

const ProfileDetails=styled.div`
width: 880%;
position: absolute;
right: -50%;
height: auto;
background-color: #3d3b38;
top: 105%;
border-radius: 5px;


img{
    width:34%;
    border-radius: 50%;
    margin: 10px auto;
    height: 100px;
}
 p{
    text-align: center;
    color: white;
}
div#settings{
    padding: 15px 0px;
}
div#settings p{
    padding: 2px 10px;
    cursor: pointer;
    color: white;
}
div#settings p:hover{
    color: dodgerblue;
}
.p-for-profile-change{
    border-bottom: 2px solid white;
}
@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ratio: 1){
    top:110%;
}
`

export default ProfileTopNav;