import React, { useState } from 'react'
import "../styles/signup.css"
import styled from 'styled-components'
import Sidebar from '../components/Sidebar'
import { request } from '../apiHandler/Authapi'
function RegisterLost() {
   const handleChange = e => {
        const { name, value } = e.target;
        setProduct({
            ...product,
            [name]: value
        })
    }
    const handleSubmit = e =>{
     e.preventDefault();
     request("lostProducts/add-product", "POST", product, {"Content-Type": "application/json", Authorization: `Bear ${localStorage.getItem("auth")}`})
     .then(data=>{
         console.log(data);
     })
     .catch(err=>console.log(err))
    }
    
    const [product, setProduct] = useState({
        productName: "",
        category: "",
        date: Date.now(),
        whereLost: "",
        payment: "",
        description: ""
    })

    const { productName, category, date, whereLost, payment, description } = product;
    return (
        <Wrapper>
            <Sidebar />
            <Container>
               <h2>Fill the form to find your lost items</h2>
               <Form onSubmit = {handleSubmit}>
                   <Input type="text" name="productName" value={productName} onChange={handleChange} placeholder="Enter item name"/>
                   <Select>
                       <Option>Choose Item Category</Option>
                   </Select>
                   <Input type="date" name="date" value={date} onChange={handleChange} placeholder="Enter date it was lost"/>
                   <Input type="text" name="whereLost" onChange={handleChange} value={whereLost} placeholder="Enter where it was lost"/>
                   <Input type="text" name="description" onChange={handleChange} value={description} placeholder="Add additional details"/>
                   <Button type="submit">Find my item</Button>
               </Form>
            </Container>
        </Wrapper>
    )
}

export default RegisterLost

const Wrapper = styled.div`
   background: #d5def5; 
   height: 100vh;
   width: 100%;
`
const Select = styled.select`
 width: 100%;
 padding: 10px 10px;
 border: 1px solid black;
 border-radius: 5px;
 margin-top: 10px;
 
`
const Option = styled.option`

`
const Container = styled.div`
   width: 60%;
   background: #fff;
   /* margin: 10% auto; */
   margin-left: 20%;
   /* margin-top: 20%; */
 
   height: 90vh;
   h2{
       text-align: center;
       font-weight: bold;
       padding-top: 30px;
   }
`
const Form = styled.form`
   width: 50%;
   margin: 5% auto;
   height: auto;
   input{
       border-radius: 5px;
   }
   input::placeholder{
       text-align: left;
   }
   input:focus{
    /* border-color: dodgerblue; */
    outline: dodgerblue; 
    border: 2px solid dodgerblue;
   }
`
const Input = styled.input`
 width: 100%;
 margin-top: 10px;
 padding: 7px 10px;
 border: 1px solid gray;
`
const Button = styled.button`
   width: 100%;
   background: #0055ff;
   color: #ffffff;
   margin-top: 10px;
   padding: 10px 10px;
   font-weight: bold;
   cursor: pointer;
   outline: none;
   border: none;
   `