import React from 'react'
import ProfileTopNav from "./Profile-topNav";
import styled from 'styled-components';

const TopNav = () => {
    const months =  ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"]
    return (

        <Container>
            <div className="flex justify-between items-center  my-4 ">
                <div className="bg-white px-1 flex  items-center justify-between w-full     rounded h-10 " id="s-cont">
               <label htmlFor="select-month">Period</label>
                <select id="select-month" 
                className="border rounded-lg mr-1 focus:border-0 border-gray-100" name="lost"
                 style ={{outline: "none"}}>
                    {
                        months.map((month,index)=>{
                            return (
                                <option key={index} value={index}>{month}</option>
                            )
                        })
                    }
                </select>
            </div>
                <div className="">
                   <ProfileTopNav margin={0} />
                </div>
            </div>        
        </Container>
    )
}

const Container=styled.div`
  /* .cont{
      align-items: center;
      display: flex;
      justify-content: space-between;
  }
  .cont .input-cont{
      width: 30%;
      display: flex;
   input{
       width: 100%;
   }
  }
  .cont .section {
      width: 70%;
  }
  #s-cont{
      width: 98.5%;
  } */
`

export default TopNav
