import React, { useEffect, useState} from 'react';
import { useQuery } from "react-query"
import ReactPaginate from "react-paginate";
import "../styles/product.css";
import styled from "styled-components";
import { Link, useHistory } from 'react-router-dom';
import TopNav from '../components/TopNav';
import Sidebar from '../components/Sidebar';
import { ArrowLeft, ArrowRight } from "@material-ui/icons"
import { API } from '../shared/context/modes';
import {categories} from "../shared/utils/Categories";
import Loader from "../components/Loader";

const Product = () => {
    const history = useHistory();
    const productStyles = {
        backgroundColor: "#d5def5",
        marginLeft: "15%",
        minWidth:"85%",
        marginRight:"0",
        minHeight: "100vh"
    }
    const [products, setProducts] = useState([])
    const [pageNumber, setPageNumber]= useState(0);
    const productsPerPage = window.screen.width > 1000 ? 8 : (window.screen.width >500 && window.screen.width < 800 ? 6:  4);
 
    useQuery("lostProducts", ()=>fetch(`${API}/lostProducts`, { method: "GET", headers: {"bearer": `${localStorage.getItem("auth")}`}}).then(res=>res.json()), {
        onSuccess: (data)=>setProducts(data.lostProducts),
        onError: (err)=>console.log(err)
    })
   
    const pagesVisited = pageNumber * productsPerPage;
    const displayPageProducts = products.length >0 && products.slice(pagesVisited, pagesVisited + productsPerPage);
    const pageCount = Math.ceil(products.length / productsPerPage);
    const changePage = ({ selected })=>{
          setPageNumber(selected);
    }
    return (
        <div className="flex">
            <Sidebar />
            <div className="products position-relative p-2   " style={productStyles}>
             <TopNav/>

            <div className="list_of_products flex  flex-wrap sm:justify-start mb-2   sm:flex-row items-center">                {
                    displayPageProducts && displayPageProducts.length > 0 ? 
                     displayPageProducts.map((prod,index) => {
                        const image = categories.find(cat=> cat._id==prod.category._id);
                        
                        return (
                            <Link key={index}  to={"/lostproduct/"+prod._id}>
                                 <Card key={prod._id} className="product mr-2 w-32 sm:w-56  sm:last:mr-0 mb-6 sm:mr-4 mr-2 ml-2  p-2 rounded  bg-white flex justify-between flex-col sm:h-60 h-36">
                                   <Holder>
                                        <img src={`${image.image}`} className="w-28  h-28 cursor-pointer" alt="lost product" />
                                    </Holder>
                                <div className="w-full mt-2 ">
                                    <h4 className="text-sm my-2"> <strong className=" sm:inline-block">Lost at: </strong> {prod.location.split(',')[0].slice(0, 10)}</h4>
                                    <h4 className="text-sm  sm:block"> <strong>Item: </strong> {prod.category.name}</h4>
                                </div>
                                <div className="flex mt-1 sm:flex-row flex-col items-center  justify-between">
                                    <span className="money text-xs">{prod.money}</span>
                                    <span className="text-xs">{prod.lost_at}</span>
                                </div>   
                            </Card>

                            </Link>
                         )
                    }):(
                        <div className="sm:ml-60">
                            <Loader isLoading={true}/>
                            { !products && <p>No lost items registered on the platform</p>}
                        </div>
                    )
                }
            </div>
        { products.length > 0 && <ReactPaginate
            previousLabel= { <ArrowLeft />}
            nextLabel ={ <ArrowRight /> }
            pageCount = { pageCount }
            onPageChange ={changePage}
            containerClassName = { "pagination"}
            previousLinkClassName = {"previousLink"}
            nextLinkClassName = {"nextLink"}
            disabledClassName = {"paginationDisabled"}
            activeClassName = {"paginationActive"}
            activeLinkClassName = { "activeLink"}
        />}
        </div>
        </div>
    )
}

export default Product
export const Holder = styled.div`
     width: 100%;
     height: 140px;
     @media screen and (max-width: 760px){
             height: 150px !important;
         }
     img{
         height: 100% !important;
         width: 100% !important;
         max-height: 100%;
         max-width: 100%;
         object-fit: cover;
     }
`
export const Card = styled.div`
    box-shadow: 10px 10px 10px rgba(119, 112, 112, 0.3);
`