import React, {useEffect} from 'react'
import target from "../assets/target.png";
import cash from "../assets/cash-hand.png";
import kill from "../assets/kill.png";
import Aos from "aos";
import "aos/dist/aos.css";

const Services = () => {

    useEffect(() =>{
        Aos.init({duration:1000});
    })
    return (
        <div>
        <div className="my-3 opacity-80" id="services">

        <h3 className="text-center text-3xl mt-4 font-bold" id="services">Our Services</h3>
        <p className="mb-7 text-center">
              Shaka is online platform that helps the users to find their <br /> lost things by connecting them with the ones who found them.
        </p>

        <div className="flex justify-around flex-col items-center sm:flex-row">
            
              <div className="flex-col border  w-80 mt-3 p-2  flex items-center justify-around" data-aos="fade-up">
                  {/* <div className="text-5xl text-red-600 my-3">P</div> */}
                  <img src={kill} alt="" />
                  <h2 className="text-xl bold my-4">Saving Time</h2>
                  <p className="text-center">
                    Don't waste your time struggling to find your lost things. Shaka helps you to do it automatically without your presence.
                  </p>
              </div>
        
              <div className="flex-col border  w-80 mt-3 p-2  flex items-center justify-around" data-aos="fade-up">
                  {/* <div className="text-5xl text-red-600 my-3">P</div> */}
                  <img src={target} alt="" />
                  <h2 className="text-xl bold my-4">Finding Lost</h2>
                  <p className="text-center">
                  If you lost something like Driving license, what you have to do is to submit it to Shaka and then our systems will search for it.
                   
                  </p>
              </div>
      
              <div className="flex-col border  w-80 mt-3 p-2  flex items-center justify-around" data-aos="fade-up">
                  {/* <div className="text-5xl text-red-600 my-3">P</div> */}
                  <img src={cash} alt="" />
                  <h2 className="text-xl bold my-4">Make Money</h2>
                  <p className="text-center">
                  Shaka pays you for every item you have found and submit it to the system. You get paid if the owner has been found.
                  </p>
              </div>
              
        </div>
        </div>
    </div>
    )
}


export default Services
