import { useState } from 'react'
import { BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import VerifyAccount from './components/VerifyAccount';
import Product from './pages/Product';
import ProductDetails from './pages/ProductDetails';
import Signup from './pages/Signup';
import { Modes } from './shared/context/modes';
import { Store } from './shared/utils/Store';
import HomePage from './pages/HomePage';
import {Dashboard} from './pages/Dashboard';
import PrivateRoute from './components/PrivateRoute';
import { PublicRoute } from './components/PublicRoute';
import RegisterLost from './pages/RegisterLost';
import {  ToastContainer } from 'react-toastify';
import Payment from "./pages/Payment";
import 'react-toastify/dist/ReactToastify.css';
import FoundProduct from './components/FoundProducts';
import NotifyAccountVerification from './components/NotifyAccountVerification';
import ChangePassword from './components/ChangePassword'; 
import ProdDetLost from './pages/ProdDetLost';
import PasswordResetNotification from "./components/PasswordResetNotification";
import Resend from "./components/Resend";
import ForgotPassword from "./components/ForgotPassword";
import ConfirmFoundProduct from "./pages/ConfirmFoundProduct";

function App() {
    const store = Store(useState);
    
  return (
    <Modes.Provider  value ={ store }>
    <Router>
    <ToastContainer position="top-center" autoClose={5000} />
       <Switch>
       <PublicRoute component = {HomePage} path="/" exact/>
       <PublicRoute component = {Signup} path="/account" exact/>
       <PrivateRoute component = {ProductDetails} path="/product/:id" />
       <Route exact  component={VerifyAccount} path="/verifyAccount/:verificationCode"/>
       <PrivateRoute component={Dashboard}  path="/dashboard" />
       <PrivateRoute component={Product} path="/lostproducts" />
       <PrivateRoute component={FoundProduct} path="/foundproducts"/>
       <PrivateRoute component= {RegisterLost} path="/submitlost"/>
       <PrivateRoute component = {ProdDetLost} path ="/lostproduct/:id" />
       <PrivateRoute component={Payment} path="/payment"/>
       <Route component={NotifyAccountVerification} path="/verifyMessage"/>       
       <Route component={PasswordResetNotification} path="/resetMessage"/>       
       <Route component={Resend} path="/resendVerification"/>
       <Route component={ForgotPassword} path="/forgotpassword"/>
       <Route component={ChangePassword} path="/shaka/reset/:code"/>
       <Route component={ConfirmFoundProduct} path="/foundProduct/checkup/:id" />
       </Switch>
       
      
    </Router>
  
    </Modes.Provider>
  );
}

export default App;
