import React,{useState, useEffect} from "react";
import styled from "styled-components";
import Sidebar from "../components/Sidebar";
import PaymentMethods from "../components/PaymentMethods";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faMoneyCheckAlt} from "@fortawesome/free-solid-svg-icons";
import ProfileTopNav from "../components/Profile-topNav"
import Axios from "axios";

const Payment=()=>{
    const [bankMethod,setBankMethod]=useState(false);
    const [mobileMethod,setMobileMethod]=useState(true);
    const [userProducts,setUserProducts]=useState([]);
    const user=JSON.parse(localStorage.getItem("user"));
    const token=localStorage.getItem("auth");

    useEffect( async () =>{
        try{
          let res= await Axios.get("https://shaka-1.herokuapp.com/api/lostProducts/demons/user-products",{headers:{bearer:token}});
          
          setUserProducts(res.data.userProducts);
          //toast.success(res.message);
        }catch(err){
           console.log(err);
        }
      
  },[])

    function changeToMobile(){
        setMobileMethod(true);
        setBankMethod(false);
    }

    function changeToBank(){
        setMobileMethod(false);
        setBankMethod(true);
    }

  
   return(
      <Container>
         <Sidebar/>
         <ProfileTopNav margin={10}/>

         <Payments>
          <h2 className="header">Pay for your found things</h2>

          <Pay>
              <InputsTaker>
                <div className="row" id="choice-links">
                    <button onClick={changeToBank}
                    style={bankMethod?{border:"1px solid dodgerblue",backgroundColor:"rgb(132, 178, 223)",color:"white"}:{border:"1px solid grey"}}>Bank Card</button>
                    <button onClick={changeToMobile} 
                    style={mobileMethod?{border:"1px solid dodgerblue",backgroundColor:"yellow",color:"black"}:{border:"1px solid grey"}}>MTN MoMo</button>
                   
                </div>
             <PaymentMethods mobileMethod={mobileMethod} bankMethod={bankMethod} />
              </InputsTaker>

              <Details>
                  <DetailsTop>
                      <p>Your payment details</p>
                      <div className="money">
                      <h1>{userProducts.length > 0 ? userProducts[0].category.price : "0"} RWF</h1>
                      <FontAwesomeIcon icon={faMoneyCheckAlt} className="icon"/>
                      </div>
                  </DetailsTop>

                  <UserDetails>
                      <div className="userDetail">
                        <p>Account user</p>
                        <strong>{user.firstname +" " + user.lastname}</strong>
                      </div>

                      <div className="userDetail">
                        <p>Payment Method</p>
                        <strong>Mobile Money</strong>
                      </div>

                      <div className="userDetail">
                        <p>Paid items</p>
                        <strong>{userProducts.length > 0 ? userProducts[0].category.name : "Nothing to pay for"}</strong>
                      </div>

                  </UserDetails>
              </Details>
          </Pay>

         </Payments>

      </Container>
   )
}

const Container=styled.div`
width: 100%;
height: 100vh;
box-sizing:border-box;
background-color:  #d5def5;
font-family: "poppins";

@media only screen and (max-width:620px){
    height: auto;
}
@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ration:1){
    height: 100%;
}
`

const Payments=styled.div`
width: 81%;
margin-left: 18%;
background-color: white;
height: 85%;
border-radius: 5px;
margin-top: 10px;
h2.header{
    padding: 10px 6.8%;
}
@media only screen and (max-width: 620px){
    height: auto;
    margin-left: 12.5%;
    margin-top: 0px;
    width:85%;
}

@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ratio: 1) {
width: 88.5%;
margin-left: 10%;
}
`

const Pay=styled.div`
width: 100%;
margin-top: 20px;
display: flex;
flex-direction: row;
justify-content: space-evenly;
flex-wrap: wrap;

@media only screen and (max-width: 620px){
    flex-direction: column;
    padding:15px;
}

@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ratio: 1) {
padding:0px 15px;
justify-content: space-evenly;
}

`
const InputsTaker=styled.div`
width: 45%;
.row{
    display: block;
    margin: 10px 0px;
}
#choice-links{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
#choice-links button{
        border: 1px solid grey;
        padding: 10px 10px;
        border-radius: 4px;
        width: 28%;
    }
@media only screen and (max-width:620px){
        width:100%;
       #choice-links button{
            width: 45%;
            padding: 10px;
            font-size:12px;
        }
       #choice-links button.google-pay{
           display: none;
       }
    }
@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ration:1){
    width: 65%;
    #choice-links button{
            width: 50%;
            padding: 0px;
            color: red;
        }
} 
`
const Details=styled.div`
width: 35%;
background-color: dodgerblue;
border-radius: 5px;
color: white;

@media only screen and (max-width:620px){
    width:100%;
    margin-top:10px;
}
@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ration:1){
    width: 35%;
}
`
const DetailsTop=styled.div`
padding: 20px 25px;
border-bottom: 3px dashed rgb(189, 189, 189);
h1{
    padding-top: 20px;
}
div.money{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}
div.money .icon{
    margin-top: 20px;
}
`
const UserDetails=styled.div`
padding: 15px 25px;
div.userDetail{
    margin-top: 10px;
}
div.userDetail p{
    color: rgb(233, 232, 232);
}
`

export default Payment;
