import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {Provider} from "react-redux";
import {createStore} from "redux";
import {composeWithDevTools} from "redux-devtools-extension";
import rootReducer from "./reducers/rootReducer";
import { API } from './shared/context/modes';
import { QueryClient, QueryClientProvider} from "react-query"

const store=createStore(rootReducer,composeWithDevTools());
  

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      refetchOnWindowFocus: false,
    },
  },
});
queryClient.setQueryDefaults("lostProducts", { queryFn: ()=>fetch(`${API}/lostProducts`, 
    { method: "GET", headers:{"bearer" : `${localStorage.getItem("auth")}`}}).then(res=>res.json())});

queryClient.setQueryDefaults("foundProducts", { queryFn: ()=>fetch(`${API}/foundProducts`, 
    { method: "GET", headers:{"bearer" : `${localStorage.getItem("auth")}`}}).then(res=>res.json())});
// const prefetchProducts = async () =>{
//   await queryClient.prefetchQuery("foundProducts", ()=>fetch(`${API}/foundProducts`, { method: "GET", headers: {"bearer": `${localStorage.getItem("auth")}`}}).then(res=>res.json()))
// }
// console.log(prefetchProducts);
ReactDOM.render(
  <React.StrictMode>
   <QueryClientProvider client = {queryClient}>
       <App />
    </QueryClientProvider>
  </React.StrictMode>,
  document.getElementById('root')
);

