    import { Link } from 'react-router-dom';

import React from 'react'
import styled from 'styled-components';

const SimpleHeader = () => {
    return (
        <nav className="flex justify-between p-5  items-center bg-primary">
            <Link to='/'>
            <Logo>
           <svg xmlns="http://www.w3.org/2000/svg" width="40.314" height="40.314" viewBox="0 0 56.314 56.314">
         <path id="Path_2" data-name="Path 2" d="M46.441,42.521l11.873,11.87-3.923,3.923L42.521,46.441a24.955,24.955,0,1,1,3.92-3.92ZM30.215,16.352a11.089,11.089,0,1,0,7.335,7.335,5.544,5.544,0,1,1-7.335-7.338Z" transform="translate(-2 -2)" fill="#05f"/>
         </svg>

           <h2>Shaka</h2>
        </Logo>
            </Link>
        

         <Link to="/account">
         <button className="bg-btn px-7 py-1 text-white rounded-lg">Login</button>
         </Link>
         
        </nav>
    )
}

const Logo = styled.div`
           width:  80%;
           margin: 10px auto;
           display: flex;
           align-items: center;
           color:  #3d7dff;
           font-family: 'poppins';
           width: 90%;

    `

export default SimpleHeader
