import React,{useState} from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import styled from "styled-components";
import { useHistory } from "react-router-dom";
import Modal from 'react-modal';
import { Close } from "@material-ui/icons";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { request } from '../apiHandler/Authapi';
import { toast } from 'react-toastify'


const UploadModal = ({closeModal,modalIsOpen}) =>{

    const [itemPhoto, setItemPhoto] = useState('');
    const [itemPhotoPreview, setItemPhotoPreview] = useState('');
    const [isLoading,setIsLoading]=useState(false);
    const user=JSON.parse(localStorage.getItem("user"));
    const tempUser=user;

//   BEGINNING OF MODAL STUFFS
   let clientWidth=window.screen.width;
   let history=useHistory();

    const customStyles = {
      overlay: {
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0, 0, 0, 0.6)',
        zIndex:"3"
      },
        content: {
          top: clientWidth>400  ? '40%' : "40%",
          left: clientWidth>400 ? '50%' : "50%",
          right: clientWidth>400 ? '50%' : "10%",
          bottom: clientWidth>400 ? 'auto' : "auto",
          marginRight: clientWidth>400 ? '-50%' : "-55%",
          transform: 'translate(-50%, -50%)',
          background:"white"
        }
      };
      Modal.setAppElement('#root');
      let subtitle;
  
    
      function afterOpenModal() {
        // references are now sync'd and can be accessed.
        subtitle.style.color = '#f00';
      }
    

    const closePreview = () =>{
        setItemPhoto("");
        setItemPhotoPreview("");
    }

    function onChange(e){
    
        if(e.target.name === 'itemPhoto'){
            const reader = new FileReader();
            reader.onload = ()=>{
                if(reader.readyState === 2){
                    //console.log(reader.result)
                    setItemPhoto(reader.result);
                    setItemPhotoPreview(reader.result);
                }
            }
            reader.readAsDataURL(e.target.files[0])

        }
     }

    function handleSubmit(e){
         e.preventDefault();

    let profilePicture={itemPhoto};
  setIsLoading(true);
  request(`files/upload-profile`, "POST", profilePicture, {"Access-Control-Allow-Origin": "*","Content-Type": "application/json","bearer":`${localStorage.getItem('auth')}`})
      .then(res =>{
           
          setIsLoading(false);
             if(res.message){

               tempUser.profile=res.newProfile;
               localStorage.setItem("user",JSON.stringify(tempUser));

               toast.success(res.message);
               history.push("/dashboard");

             }else if(res.error){
               toast.error(res.error);
             }
            
      })
      .catch(err =>{
          console.log(err);
        toast.error("Unexpected error occurred!");
      })
        }


    return (
       
        <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Upload picture"
      >
        <h2 className="hidden" ref={(_subtitle) => (subtitle = _subtitle)}>x</h2>
        <p style={{paddingBottom:"5px"}}>Hello, {user.firstname + " " + user.lastname}</p>
         <FontAwesomeIcon icon={faTimes} onClick={closeModal} style={{position:"absolute",top:"10px",right:"10px",color:"red",cursor:"pointer"}}/>
        
        <form onSubmit={handleSubmit} >
            <div className="upload-container">
                <label htmlFor="photo">Upload Profile Picture</label>
                <Input type="file" name="itemPhoto" onChange={ onChange }/>
        { itemPhoto ?
            <Preview>
                   <img src = {itemPhotoPreview} alt="user-profile"/>
                   <ClosePreview onClick={closePreview}/>
                </Preview>
                : null
        }
                <Button type="submit">{ !isLoading?"Upload profile picture" : <div className="loader"></div>} </Button>
            </div>
        </form>
      </Modal>
   
    )
}

const Input = styled.input`
 width: 100%;
 margin-top: 10px;
 padding: 7px 10px;
 border: 1px solid gray;
`

const Preview = styled.div`
      width: 200px;
      height: 250px;
      position: relative;
      padding: 7px;
      img{
          width: 100% !important;
          height: 100% !important;
          object-fit: cover;
          border-radius: 5px;
      }
`
const ClosePreview = styled(Close)`
      position: absolute;
      top: 0px;
      right: 0px;
      border-radius: 50%;
      background: #fff;
      font-size: 20px !important;
      color: red;
      cursor: pointer;
`
const Button = styled.button`
   width: 100%;
   background: #0055ff;
   color: #ffffff;
   margin-top: 10px;
   padding: 10px 10px;
   font-weight: bold;
   cursor: pointer;
   outline: none;
   border: none;
   border-radius: 3px;
  .loader{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 5px solid lightgray;
    border-top: 5px solid #5e93ff;
    animation: spinner 1s linear infinite; 
    margin: 0px auto;
  }
  @keyframes spinner{
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }

   `

export default UploadModal;