

export const foundProductsReducer=(state={},action)=>{
    switch (action.type) {
        case 'FOUND_PRODUCTS':
            return {...state,...action.payload};
        default:
            return state;
    }
};


export const lostProductsReducer=(state={},action)=>{
    switch (action.type) {
        case 'LOST_PRODUCTS':
            return {...state,...action.payload};
        default:
            return state;
    }
};



